.keystroke, kbd {
  color: #222;
  font-family: Consolas, Menlo, Courier, monospace;
  font-size: inherit;
  background-color: #ededed;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin: 0;
  padding: .125rem .25rem 0;
}

.switch {
  user-select: none;
  border: none;
  outline: 0;
  margin-bottom: 1.5rem;
  padding: 0;
  position: relative;
}

.switch label {
  color: #0000;
  cursor: pointer;
  text-indent: 100%;
  width: 4rem;
  height: 2rem;
  background: #ddd;
  margin-bottom: 1rem;
  transition: left .15s ease-out;
  display: block;
  position: relative;
}

.switch input {
  opacity: 0;
  padding: 0;
  position: absolute;
  top: 9px;
  left: 10px;
}

.switch input + label {
  margin-left: 0;
  margin-right: 0;
}

.switch label:after {
  content: "";
  height: 1.5rem;
  width: 1.5rem;
  -o-transition: translate3d(0, 0, 0);
  background: #fff;
  transition: left .15s ease-out;
  display: block;
  position: absolute;
  top: .25rem;
  left: .25rem;
  transform: translate3d(0, 0, 0);
}

.switch input:checked + label {
  background: #2ba6cb;
}

.switch input:checked + label:after {
  left: 2.25rem;
}

.switch label {
  height: 2rem;
  width: 4rem;
}

.switch label:after {
  height: 1.5rem;
  width: 1.5rem;
}

.switch input:checked + label:after {
  left: 2.25rem;
}

.switch label {
  color: #0000;
  background: #ddd;
}

.switch label:after {
  background: #fff;
}

.switch input:checked + label {
  background: #2ba6cb;
}

.switch.large label {
  height: 2.5rem;
  width: 5rem;
}

.switch.large label:after {
  height: 2rem;
  width: 2rem;
}

.switch.large input:checked + label:after {
  left: 2.75rem;
}

.switch.small label {
  height: 1.75rem;
  width: 3.5rem;
}

.switch.small label:after {
  height: 1.25rem;
  width: 1.25rem;
}

.switch.small input:checked + label:after {
  left: 2rem;
}

.switch.tiny label {
  height: 1.5rem;
  width: 3rem;
}

.switch.tiny label:after {
  height: 1rem;
  width: 1rem;
}

.switch.tiny input:checked + label:after {
  left: 1.75rem;
}

.switch.radius label {
  border-radius: 4px;
}

.switch.radius label:after {
  border-radius: 3px;
}

.switch.round {
  border-radius: 1000px;
}

.switch.round label, .switch.round label:after {
  border-radius: 2rem;
}

