  position: relative;
  overflow: hidden;
}

@media (max-width: 650px) {
  .portfolioItem figure {
    padding-bottom: 50%;
  }
}

.portfolioItem figure figcaption {
  opacity: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, #ffffffb3, #ffffffb3);
  transition: all .8s;
  position: absolute;
  top: 0;
}

.portfolioItem figure figcaption p {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  transition: all .6s;
  position: absolute;
  top: -200px;
}

@media (max-width: 650px) {
  .portfolioItem figure figcaption {
    opacity: 1;
  }

  .portfolioItem figure figcaption p {
    font-size: 1.2rem;
    top: calc(50% - .75rem);
  }
}

.portfolioItem figure .squareImage {
  width: 100%;
  filter: opacity(20%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 100%;
  transition: all .8s;
  position: absolute;
  top: 0;
}

@media (max-width: 650px) {
  .portfolioItem figure .squareImage {
    filter: opacity();
    padding-bottom: 50%;
